import React, { Component } from 'react'

export default class About extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-about" data-section="about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-12">
                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInTop">
                <div className="col-md-12">
                    <div className="about-desc">
                    <span className="heading-meta">About Me</span>
                    <h2 className="colorlib-heading">My name is Zahid Nur Al Azami</h2>
                    <p>Zahid is a Bachelor of Computer Science who <b>loves to cook data</b> and believes that <b>data is the best decision-maker</b>. Now, he is focusing on his career to become a <b>Great Data Analyst.</b> <br></br>
                    Apart from doing Data Analysis, he is doing <b>Data Utilization for marketing efforts</b> like Digital Email Marketing and App Push Notifications. Not only do the efforts, but he is also responsible to <b>maintain the marketing performance.</b><br></br>
                    
                    Besides that, he is also creating a <b>Front-end</b> and <b>Dashboard for Data Visualization.</b><br></br><br></br>
                    
                    Capable of using some programming languages, technologies, and frameworks such as <b>Python, BigQuery, PostgreSQL, Google Data Studio, Google Analytics, Tableau, HTML, CSS, etc.</b> </p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        <section className="colorlib-about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">My Education</span>
                <h2 className="colorlib-heading">My Educational Journey:</h2>
            </div>  
            </div>
            <div className="row row-pt-md">
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                <span className="icon">
                    <i className="icon-study" />
                </span>
                <div className="desc">
                    <h3>Gadjah Mada University, Yogyakarta, Indonesia</h3>
                    <p><b>Bachelor of Computer Science.</b><br></br>
                    Graduated in 2020 with GPA <b>3.47</b>/4.0</p>
                    <div className="text-justify">
                        <p><b>Relevant Coursework:</b> Web Programming, Software Development, Information Technology Project Management, Machine Learning, Data Mining and Business Intelligence, and Artificial Intelligence.</p>
                    </div>
                    <div className="text-justify">
                        <p><b>Thesis:</b> Fire Detection System Based on Internet of Things.</p>
                    </div>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
        <section className="colorlib-about">
        <div className="colorlib-narrow-content">
            <div className="row">
            <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">Skills</span>
                <h2 className="colorlib-heading">Some skills that I mastered:</h2>
            </div>
            </div>
            <div className="row row-pt-md">
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                <span className="icon">
                    <i className="icon-data" />
                </span>
                <div className="desc">
                    <h3>Data Analysis</h3>
                    <p>Capable of using some programming languages and technologies such as <b>Python, BigQuery, PostgreSQL, and Google Analytics</b></p>
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                <span className="icon">
                    <i className="icon-data" />
                </span>
                <div className="desc">
                    <h3>Data Visualization</h3>
                    <p>Capable of using some tools and technologies such as <b>Google Data Studio and Tableau</b></p>
                </div>
                </div>
            </div>
            <div className="col-md-4 text-center animate-box">
                <div className="services color-5">
                <span className="icon">
                    <i className="icon-pen2" />
                </span>
                <div className="desc">
                    <h3>Front-end</h3>
                    <p>Capable of using some programming languages and technologies such as <b>HTML, CSS, Javascript, and React</b></p>
                </div>
                </div>
            </div>
            </div>
        </div>
        </section>
      </div>
    )
  }
}
