import React, { Component } from 'react'

export default class Oe extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="oe">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInTop">
                <span className="heading-meta">Organizational Experiences</span>
                <h2 className="colorlib-heading animate-box">Various organizational, event, and volunteer experiences that I have done:</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-globe" />
                      </div>
                      <div className="timeline-label">
                        <h2>Community Service Program, Gadjah Mada University   <span>June 2019 - August 2019</span></h2>
                        <p>As a <b>Team Leader</b> in Manyeuw, Southeast Maluku, Maluku, Indonesia. <br></br>
                           Stay there for approximately 52 days: carry out various work and service programs in accordance with the needs of the region.</p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-user2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Computer Science Undergraduate Student Association, Gadjah Mada University   <span>October 2016 - December 2017</span></h2>
                        <p><b>Member of the Internal External Division</b> that is in charge of public relations.</p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-deviantart" />
                      </div>
                      <div className="timeline-label">
                        <h2>Delayota Art #9   <span>December 2014 - December 2015</span></h2>
                        <p>One of the biggest art events in Yogyakarta, Indonesia which is managed by school students.<br></br>
                           As a <b>member of the Event Division</b> that is in charge of preparing the event's concept.</p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-user" />
                      </div>
                      <div className="timeline-label">
                        <h2>Student Council   <span>October 2014 - November 2015</span></h2>
                        <p>As a <b>Chairman</b> of the Student Council at 8 Senior High School Yogyakarta, Indonesia.<br></br>
                           Responsible for all student activities in the senior high school.</p> 
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
