import React, { Component } from 'react'

export default class Portfolio extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="portfolio">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInTop">
                <span className="heading-meta">My Portfolios</span>
                <h2 className="colorlib-heading animate-box">Some of projects I have worked on:</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-globe" />
                      </div>
                      <div className="timeline-label">
                        <h2>Cookiepocalypse Project  <span>On Going</span></h2>
                        <p>Monitoring and being responsible for internal first-party data quality.</p>
                      </div>
                    </div>
                  </article>
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-cup" />
                      </div>
                      <div className="timeline-label">
                        <h2>Recalling Memories From World Cup 2018 Squads Data  <span>2021</span></h2>
                        <p>Recalling some nostalgic memories from World Cup 2018. Trying to find out some interesting facts that maybe we don’t know before.<br></br>
                        The details of the project could be accessed <a href="https://zahidna.medium.com/recalling-memory-from-world-cup-2018-squads-data-4707e166c909" target="_blank" rel="noopener noreferrer">here.</a></p>
                      </div>
                    </div>
                  </article>
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-youtube" />
                      </div>
                      <div className="timeline-label">
                        <h2>Youtube Trending Videos Data Analysis  <span>2021</span></h2>
                        <p>Trying to figure out how Youtube Trending works with Youtube trending videos data analysis.<br></br>
                        The details of the project could be accessed <a href="https://medium.com/codex/youtube-trending-videos-data-analysis-a340b4a039f9" target="_blank" rel="noopener noreferrer">here.</a></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-device-laptop" />
                      </div>
                      <div className="timeline-label">
                        <h2>Fire Detection System Based on Internet of Things (IoT)  <span>2020</span></h2>
                        <p>The research used the Mamdani fuzzy logic method with the mean of maximum defuzzification method. This method is combined with Internet of Things technology, thus enabling the IoT platform to communicate with other devices. In addition, a bot telegram messenger was designed as a notification system and IoT platform control system. The program is designed using the Python on the Raspberry Pi device.<br></br>
                        Kindly check the demo <a href="https://www.youtube.com/playlist?list=PLnJYXPguyehbjNY7ODyJqRQalWlnxYWvK" target="_blank" rel="noopener noreferrer">here.</a> The details of the project could be accessed <a href="http://etd.repository.ugm.ac.id/penelitian/detail/192403" target="_blank" rel="noopener noreferrer">here.</a></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
