import React, { Component } from 'react'

export default class We extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="we">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInTop">
                <span className="heading-meta">Work Experiences</span>
                <h2 className="colorlib-heading animate-box">Various work that I have done:</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-data" />
                      </div>
                      <div className="timeline-label">
                        <h2>OVAL Data, Kompas Gramedia   <span>June 2021 – Now</span></h2>
                        <p>Became <b>Data Analyst</b> at Oval Data Kompas Gramedia.<br></br>
                        Oval Data is a division that acts as a ‘Customer Data Platform’ (CDP) on KG Group and had all data of each business unit on KG Group integrated on them. Besides helping the digitalization process of KG Group, Oval Data also handles data utilization for some marketing efforts that could generate revenue or traffic for KG Group.</p>
                        <p>Responsibility:</p>
                        <ul>
                            <li>
                            As a Data PIC of KG Media like Kompas.com, Grid.ID, Tribun Network, Kompasiana, Bolasport, etc.
                            </li>
                            <li>
                            Provided a meaningful analysis and fresh insight for the board, product, marketing, and publisher team to support the decision-making on KG Media. Mostly related to the user activities, marketing effort, and generated traffic of KG Media.
                            </li>
                            <li>
                            Worked on the marketing efforts of KG Media Group and was responsible to maintain the performance of the marketing efforts in a continuous way.
                            </li>
                            <li>
                            Provided an ad-hoc request for Kompas Gramedia Group Team in general that related to data of KG Media and marketing efforts that utilize the data of KG Media.
                            </li>
                            <li>
                            Responsible for Kompas Gramedia Customer Data Platform (all data of Kompas Gramedia Group Business Unit) related to the monitoring of internal first-party data quality, including the tracking of users and traffic activities.
                            </li>
                        </ul>
                        <p>Achievements:</p>
                        <ul>
                            <li>
                            Troubleshooting some discrepancy data issues on Kompas Gramedia Customer Data Platform: tackle <b>± 25% of data discrepancy</b> that happened before.
                            </li>
                            <li>
                            Successfully monitoring the internal first-party data quality and <b>reaching ± 68% Sync Rate</b> of first-party data. (Internal KPI: 55%)
                            </li>
                            <li>
                            In-house marketing platform system performance monitoring that could decrease <b>± 70% of the company’s expenses</b> from CRM platform subscription.
                            </li>
                            <li>
                            Set up and maintain the running of email marketing for some KG Media Networks like Kompas.com, Grid.ID, Tribunnews, and Medio Podcast. The email marketing performance was <b>relatively stable above the benchmark of the media industry standard</b>: ± 20.8% for the open rate and ± 3.6% for the click rate.
                            </li>
                        </ul>
                      </div>
                    </div>
                  </article>
                <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-device-desktop" />
                      </div>
                      <div className="timeline-label">
                        <h2>YOGYAKOMTEK 2019   <span>August 2019 - October 2019</span></h2>
                        <p>Became <b>Chairman</b> of the Organizing Committee for the Largest Computer Exhibition in Yogyakarta.<br></br>
                          In a collaboration between Computer Science Undergraduate Students of Gadjah Mada University and APKOM (Computer Trader Association) Yogyakarta.</p>
                        <p>Responsibility:</p>
                        <ul>
                            <li>
                            As chairman of the field officer during the computer exhibition and bazaar.
                            </li>
                            <li>
                            Become a liaison between field officers, computer traders, and the computer trader associations.
                            </li>
                        </ul>
                        <p>Achievements:</p>
                        <ul>
                            <li>
                            <b>± 25% more visitors</b> visited the computer exhibition and bazaar compared to the previous year.
                            </li>
                            <li>
                            <b>± 30% more transactions</b> happened in the computer exhibition and bazaar compared to the previous year.
                            </li>
                        </ul>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-device-laptop" />
                      </div>
                      <div className="timeline-label">
                        <h2>JOINTS 2019   <span>December 2018 - May 2019</span></h2>
                        <p>Became <b>Chairman</b> of a large-scale event presented by Computer Science Undergraduate Students of Gadjah Mada University.<br></br>
                            Jogja Information and Technology Session (JOINTS) invites users of information technology all over Indonesia
                            to be wiser and smarter in the utilization of the information technology currently available.</p>
                        <p>Responsibility: leads the team to carry out the event including Tech Talk and Computer Programming Competitions.</p>
                        <p>Achievements:</p>
                        <ul>
                            <li>
                            <b>Better quality and more participants</b> for the Tech Talk than the previous year.
                            </li>
                            <li>
                            <b>More participants and higher prizes</b> for the Computer Programming Competitions than the previous year.
                            </li>
                        </ul>     
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}